import {Member} from "@/data/Member";
import {Units} from "@/data/units";
import {Party} from "@/data/Party";

export function ShowUnitsAvailable(party: Party) {
    const groups: {[key: string]: Array<string>} = {};
    Object.entries(Units).forEach(([name, unit]) => {
        if (groups[unit.house] === undefined) {
            groups[unit.house] = [];
        }
        groups[unit.house].push(name);
    });
    return groups;
}
