import {Party} from "@/data/Party";


export function NormalizeSave (data: Partial<Party>): Party {

    // convert "Byleth" to "Byleth M"
    data.members!.forEach((member, i) => {
        if (member[0] === 'Byleth' || member[0] === 'Byleth M') data.members![i][0] = 'Byleth (M)';
    });
    data.members!.forEach((member, i) => {
        if (!member[2]) {
            data.members![i][2] = [...Array(12)].map(() => Math.random().toString(36)[2]).join('');
        }
    });

    return data as Party;
}
