
















import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import {Member} from "@/data/Member";
import {House, HouseLookup, Units} from "@/data/units";
import {ShowUnitsAvailable} from "@/data/actions/ShowUnitsAvailable";
import {Party} from "@/data/Party";

@Component
export default class PartyMember extends Vue {
    @Prop() value!: string;
    @Prop() party!: Party;
    @Prop() dropdownId!: string;


    get memberName () {
        return this.value;
    }

    get unitList () {
        return ShowUnitsAvailable(this.party);
    }

    get unitData () {
        return Units[this.memberName];
    }

    // @Watch('value')
    // syncDown(){
    //     this.member = this.value;
    // }
    //
    // @Watch('member')
    // syncUp() {
    //     this.$emit('input');
    // }

    houseName(code: House): string {
        return HouseLookup[code];
    }
}
