import {Classes, Tier} from "@/data/classes";
import {Units} from "@/data/units";

// to-do: add special handling for locked classes
export function ShowClassesAvailable(unit: keyof typeof Units, tier: Tier) {

    if (tier === 'starter') {
        return [Units[unit].startingClass];
    }

    return Object.entries(Classes)
        .filter(([name, unitClass]) => unitClass.tier === tier)
        .filter(([name, unitClass]) => !unitClass.must_be || unitClass.must_be.includes(unit))
        .filter(([name, unitClass]) => Units[unit].sex === 'f' ?
            !unitClass.tags.split(',').includes('male') :
            !unitClass.tags.split(',').includes('female'))
        .map(([name, unitClass]) => name);
}
