import Vue from 'vue'
import VueRouter from 'vue-router'
import Planner from '../views/Planner.vue'
import Default from "@/views/Default.vue";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Default,
  },
  {
    path: '/:store',
    name: 'Planner',
    component: Planner,
    props: true,
  },
];

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
