















import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import {Member} from "@/data/Member";
import {Units} from "@/data/units";
import {Classes} from "@/data/classes";

@Component
export default class ClassField extends Vue {
    @Prop() value!: string;
    @Prop() options!: Array<keyof typeof Classes>;
    @Prop({default: false}) disabled!: boolean;
    @Prop({default: true}) showBlank!: string;

    @Watch('options')
    adjustToOptionChange () {
        if (!this.options.includes(this.value) && this.value !== null && this.value !== '' && this.value !== undefined) {
            console.log('auto-switching', this.value);
            this.$nextTick(() => {
                // if there's only one option, then it's the starter class, and just snap to it; otherwise, clear
                if (this.options.length === 1) {
                    this.$emit('input', this.options[0]);
                } else {
                    this.$emit('input', '');
                }
            })
        }
    }
}
